import Vue from 'vue'
import Router from 'vue-router'
import Homepage from '../components/Homepage'
import ProjectPage from '../components/ProjectPage'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: '/',
  routes: [
    {
      path: '/',
      name: 'HomePage',
      component: Homepage
    },
    {
      path: '/aston-martin-website',
      name: 'AstonMartin',
      component: ProjectPage
    },
    {
      path: '/stoneacre-leasing-website',
      name: 'StoneacreLeasing',
      component: ProjectPage
    },
    {
      path: '/stoneacre-website',
      name: 'Stoneacre',
      component: ProjectPage
    },
    
  ]
})



