<template>
    <article class="page-article">
        <section :style="{'min-height': blockMinHeight + 'px'}" class="row align-center align-middle homepage-title-section">
            <div class="small-12">
                <h1>JAMES RAWDING</h1>
                 <span class="heading-line">
                    <h2>FRONT END DEVELOPER</h2>
                </span> 
            </div>
        </section>

        <div class="homepage-carousel-outer">
            <section :style="{'min-height': blockMinHeight + 'px'}" class="homepage-carousel-section">
            <span class="heading-line">
                <h2>PREVIOUS WORK</h2>
            </span>
            <slick ref="slick" :options="slickOptions">
                <div v-for="project in projects" :key="project.id" class="slide">
                    <router-link :to="{ name: project.projectAlias }">
                        <project-heading :headingColor="project.projectColor" :projectTitle="project.projectName" :projectImagePath="project.projectImage"></project-heading>
                    </router-link>
                </div>
            </slick>
            </section>
        </div>

        <section :style="{'min-height': blockMinHeight + 'px'}" class="homepage-about-section">
            <span class="heading-line">
                <h2>HELLO</h2>
            </span>
            <div v-html="aboutMeBlurb"></div>
        </section>
        
        <section :style="{'min-height': blockMinHeight + 'px'}" class="homepage-skills-section">
            <span class="heading-line">
                <h2>SKILLS</h2>
            </span>
            <ul>
                <li v-for="skill in techSkills" :key="skill.id">{{skill}}</li>
            </ul>
        </section>
    </article>
</template>

<script>
import ProjectHeading from '@/components/ProjectHeading'
import Slick from 'vue-slick'
import {projects} from '../projects'
export default {
    components: {
        ProjectHeading,
        Slick
    },
    data() {
        return {
            projects: projects.projects,
            blockMinHeight: 0,
            headerHeight: 85,
            slickOptions: {
                slidesToShow: 1,
                dots:true,
                draggable:true,
                prevArrow: '<button type="button" class="slick-prev"><img src="static/arrow-left.png"></button>',
                nextArrow: '<button type="button" class="slick-next"><img src="static/arrow-right.png"></button>'
                // Any other options that can be got from plugin documentation
            },
            aboutMeBlurb: `<p>I'm James, Front End Developer, Mountain Biker and former Sandwich Artist based in Sheffield.</p>
                           <p>I've built this site to showcase some of the projects I have worked on. Enjoy!</p>`,
            techSkills:['HTML','CSS','SASS','Jquery','Javascript','VUEjs','UX','Git','Photoshop','Illustrator','Nunchuck Skills']
        };
    },
    // All slick methods can be used too, example here
    methods: {
        next() {
            this.$refs.slick.next();
        },

        prev() {
            this.$refs.slick.prev();
        },

        reInit() {
            // Helpful if you have to deal with v-for to update dynamic lists
            this.$nextTick(() => {
                this.$refs.slick.reSlick();
            });
        },
        getWindowHeight(event) {
            this.headerHeight = document.getElementById('headerHeight').offsetHeight;
            this.blockMinHeight = window.innerHeight - this.headerHeight;
        }
    },
    mounted(){
        this.headerHeight = document.getElementById('headerHeight').offsetHeight;
        this.blockMinHeight = window.innerHeight - this.headerHeight;

        
        // this.$nextTick(function() {
        //     window.addEventListener('resize', this.getWindowHeight);

        //     //Init
        //     this.getWindowHeight()
        // })
    },
    // beforeDestroy() {
    //     window.removeEventListener('resize', this.getWindowHeight);
    // }
    
}
</script>






