<template>
    <section class="project-heading" :style="{background: headingColor}">
        <div><img :src= "projectImagePath" :alt="projectTitle"></div>
        <h1 :style="{borderColor: headingColor}">{{projectTitle}}</h1>      
    </section>  
</template>

<script>
import Vue from 'vue'
export default

Vue.component('project-heading', {
  props: ['projectTitle', 'projectImagePath', 'headingColor'],
})
</script>



