
<template>
    <article class="page-article">
        <div v-for="project in filterProject(this.$route.name)" :key="project.id">
            <project-heading 
                    :projectTitle="project.projectName"
                    :projectImagePath="project.projectImage"
                    :headingColor="project.projectColor"
                ></project-heading>

        </div>
        <section class="project-body" v-for="project in filterProject(this.$route.name)" :key="project.id">
            <div v-html="project.projectCopy"></div>
        </section>
        
    </article>
</template>

<script>
  import ProjectHeading from '@/components/ProjectHeading'
  import {projects} from '../projects'

export default {

    name: 'project-page',
    data() {
        return {
            projects: projects.projects,
            components:{
                ProjectHeading
            }
        }
        
    },
    methods:{
        filterProject: function(projectAlias){
            return this.projects.filter(function(project){
                return project.projectAlias === projectAlias
            })
        }
    }
}

</script>



