<template>
  <header>
    <div id="headerHeight" class="row align-justify align-middle header-inner">
      <span @click="deactivateNav" class="flex-child-grow header-logo">
        <router-link :to="{ name: 'HomePage'}">JAMES RAWDING</router-link>
      </span>
      <transition name="fade" mode="out-in">
        <span v-if="!navActive" @click="activateNav" class="header-nav-title">
          PORTFOLIO
        </span>
      </transition>
        <transition name="fade" mode="out-in">
        <span v-if="navActive" @click="deactivateNav" class="header-nav-close">
          ClOSE
        </span>
        </transition>
    </div>

    <nav class="row align-middle">
      <div :class="{'nav-active': navActive, 'nav-inactive': !navActive}" class="align-middle align-center">
        <ul class="nav-overlay">
          <li v-for="project in projects" :key="project.id" @click="deactivateNav">
            <router-link :to="{ name: project.projectAlias}">{{project.projectName}}</router-link>
          </li>
        </ul>
      </div>
    </nav>
  </header>
</template>

<script>
import {projects} from '../projects'
export default {

  name: 'navigation',
  data() {
    return {
      projects: projects.projects,
      navActive: false
    }
  },
  methods:{
    activateNav: function(){
      this.navActive = true;
        document.body.className = 'no-scroll';
    },
    deactivateNav: function(){
      this.navActive = false
      document.body.className = '';
    }
  }
}

</script>





